.main-container {
    height: 100vh;
    width: 100px;
    z-index: 1;
    flex-grow: 1;
    background-color: rgba(34, 34, 34, 0.6);
    backdrop-filter: blur(7px);
}

.banner {
    height: 300px;
    position: relative;
}

.banner .banner-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: grayscale(100);
}

.banner .bottom-layer {
    position: absolute;
    width: 100%;
    height: 5rem;
    bottom: 0;
    background-image: linear-gradient(transparent, rgba(0, 0, 0, 0.9));
}

.banner .content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 20px 30px;
    display: flex;
    flex-direction: column;
    z-index: 3;
}

.banner .content .bread-crump {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.banner .content .bread-crump p {
    color: #848484;
    font-size: 14px;
    font-weight: 600;
    letter-spacing: 1px;
}

.banner .content .bread-crump p span {
    color: #f1f1f1;
}

.banner .content .bread-crump i {
    font-size: 18px;
    cursor: pointer;
}

.banner .content .artist {
    margin-top: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.banner .content .artist .left .name {
    display: flex;
    align-items: center;
}

.banner .content .artist .left .name  h2 {
    font-size: 50px;
}

.banner .content .artist .left .name img {
    width: 20px;
    margin-left: 10px;
}

.banner .content .artist .left p {
    font-size: 14px;
}

.banner .content .artist .left p i {
    margin-right: 6px;
}

.banner .content .artist .left p span {
    color: #848484;
    margin-left: 5px;
}

.banner .content .artist .right {
    display: flex;
    align-items: center;
}

.banner .content .artist .right a {
    text-decoration: none;
    display: flex;
    align-items: center;
    padding: 10px 30px;
    font-size: 16px;
    color: #f1f1f1;
    margin: 0px 15px;
    border-radius: 100vh;
    font-weight: bold;
    text-align: center;
}

.banner .content .artist .right a i {
    margin-right: 10px;
    font-size: 12px;
}

.banner .content .artist .right a:nth-child(1) {
    background-color: #39aa24;
    margin-left: 0;
}

.banner .content .artist .right a:nth-child(2) {
    background-color: rgba(0, 0, 0, 0.3);
    margin-right: 0;
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.banner .content .artist .right a:hover {
    box-shadow: 5px 10px 20px rgba(0, 0, 0, 0.4);
}

.menu-list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 25px;
    background-color: #2c2c2c;
}

.menu-list ul {
    display: flex;
    align-items: center;
    list-style-type: none;
}

.menu-list ul li {
    margin: 0 20px;
    position: relative;
}

.menu-list ul li a {
    text-decoration: none;
    color: #848484;
    font-size: 14px;
    font-weight: bold;
}

.menu-list ul li::before {
    content: '';
    position: absolute;
    bottom: -20px;
    width: 0px;
    height: 5px;
    border-radius: 5px;
    background-color: #f1f1f1;
}

.menu-list ul li:hover::before,
.menu-list ul li.active::before {
    width: 20px;
    transition: width 0.3s ease;
}

.menu-list ul li a:hover,
.menu-list ul li.active a  {
    color: #f1f1f1;
    transition: color 0.3s ease;
}

.menu-list p {
    font-size: 14px;
}

.menu-list p i {
    margin-right: 6px;
    font-size: 12px;
}

.menu-list p span {
    font-size: 12px;
    margin-left: 6px;
    color: #848484;
}

/* Main Audio List Styles */
.audio-list {
    padding: 10px 30px;
}

.audio-list .title {
    font-size: 18px;
}

.audio-list .title span {
    color: #848484;
    font-size: 12px;
}

.songs-container {
    height: 180px;
    overflow-y: scroll;
    overflow-x: hidden;
}

.songs {
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: 600;
    color: #848484;
    width: 100%;
    margin: 10px 0;
    padding: 5px;
}

.songs:hover, .songs.active {
    color: #f1f1f1;
    transition: 0.3s;
}

.count {
    margin-right: 10px;
}

.song {
    display: flex;
    align-items: center;
    width: 100%;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    padding-bottom: 5px;
}

.song .img-box {
    width: 45px;
    height: 45px;
    min-width: 45px;
    border-radius: 0.5rem;
    background-color: #555;
    margin-right: 10px;
    overflow: hidden;
}

.song .img-box img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.song .section {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}

.song .section .song-name {
    width: 50%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #a5a3a3;
}

.span-artist {
    color: #636060;
}

.song .section .hits {
    display: flex;
    justify-content: space-between;
    align-items: center;
}


.song .section .hits .hit i,
.song .section .hits .duration {
    font-size: 12px;
    margin-right: 5px;
}

.song .section .hits .hit,
.song .section .hits .duration {
    margin-right: 15px;
}

.song .section .hits .favorite i {
    color: #49e12e;
    filter: drop-shadow(0px 0px 4px #49e12e);
}


@media(max-width: 900px) {
    .banner {
        height: 40%;
    }

    .menu-list {
        height: 7%;
    }

    .menu-list ul li::before {
        bottom: -70%;
    }

    .audio-list {
        height: 53%;
    }

    .songs-container {
        height: 70%;
    }
}



@media(max-width:550px){
    .main-container{
        min-width: 380px;
    }

    .content {
        padding: 10px 15px;
    }
    
    .banner .content .artist .left .name h2 {
        font-size: 25px;
    }

    .banner .content .artist .right a {
        padding: 5px 15px;
        font-size: 12px;
        margin: 0 10px;
    }

    .menu-list {
        padding: 0 10px;
    }

    .menu-list ul li {
        margin: 0 5px;
    }

    .menu-list p {
        font-size: 10px;
    }

    .menu-list p i {
        font-size: 10px;
    }

    .audio-list {
        padding: 10px 5px;
    }

    .audio-list .songs .song .section {
        flex-direction: column;
        align-items: flex-start;
    }

    .audio-list .songs .song .section .song-name {
        width: 100%;
    }

    .audio-list .songs .song .section .hits {
        justify-content: space-between;
        margin-left: 10px;
        width: 100%;
    }

    .init-duration {
        display: none;
    }

    .hit {
        display: none;
    }

    .song .section .song-name {
        flex-direction: column;
        align-items: flex-start;
    }

    .favorite {
        display: none;
    }
    
    
}